/**
 * Utility classes
 */

.a11y {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px);
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  white-space: nowrap;
  overflow: hidden;
  display: block;
  position: absolute;
  background: #000;
  color: #fff;
}

.alignfull {
  width: auto;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
}

.is-hidden {
  display: none;
}

.is-bold {
  font-weight: 500;
}

.is-italic {
  font-style: italic;
}
